<template>
    <div class="createInvoice">
        <div class="head">开票信息填写</div>
        <div class="ul nomargin">
            <div class="li">
                <label class="lb">开票金额</label>
                <span class="val price">¥{{orderInfo.price || "--"}}</span>
            </div>
            <div class="li vt">
                <div class="vl">
                    <label class="lb">起始地址</label>
                    <span class="val">{{orderInfo.startAddress}} -- {{orderInfo.endAddress}}</span>
                </div>
                <div class="vl">
                    <label class="lb">服务时间</label>
                    <span class="val">{{orderInfo.serviceTime || "-"}}</span>
                </div>
            </div>
        </div>
        <div class="ul">
            <div class="li">
                <label class="lb">发票类型</label>
                <span class="val">电子发票</span>
            </div>
            <div class="li">
                <label class="lb">抬头类型</label>
                <div class="ttlx">
                    <span class="tti" :class="{checked: invoiceTitleType == 1}" @click="chooseInvoiceTitleType(1)">个人</span>
                    <span class="tti" :class="{checked: invoiceTitleType == 2}" @click="chooseInvoiceTitleType(2)">企业</span>
                </div>
            </div>
            <div class="li">
                <label class="lb">抬头名称</label>
                <input class="input" type="text" placeholder="请填写抬头名称（必填)" v-model="invoiceTitle">
            </div>
            <template v-if="invoiceTitleType == 2">
                <div class="li">
                    <label class="lb">纳税人识别号</label>
                    <input class="input" type="text" placeholder="需要与抬头准确匹配（必填)" v-model="nsrsbh">
                </div>
                <div class="li">
                    <label class="lb">单位地址</label>
                    <input class="input" type="text" placeholder="请填写单位地址（选填)" v-model="address">
                </div>
                <div class="li">
                    <label class="lb">开户银行</label>
                    <input class="input" type="text" placeholder="请填写开户银行（选填)" v-model="yhmz">
                </div>
                <div class="li">
                    <label class="lb">银行账户</label>
                    <input class="input" type="text" placeholder="请填写银行账户（选填)" v-model="yhzh">
                </div>
            </template>
        </div>
        <div class="ul">
            <div class="li">
                <label class="lb">电子邮箱</label>
                <input class="input" type="text" placeholder="请输入电子邮箱（必填)" v-model="email">
            </div>
        </div>
        <div class="tips"> </div>
        <div class="submitBtn" :class="{gray: !canSubmit}" @click="submit">提交</div>
        <!---->
        <message :msg="message" :show="showMesage" @close="onCloseMessage"></message>
        <loading v-if="showLoading"></loading>
    </div>
</template>

<script>
import API from "@/api";
import loading from "@/commonComponents/loading.vue";
import message from "@/commonComponents/message.vue";
// import testOrderInfo from "./testOrderInfo.js";

export default {
    components: {
        loading,
        message,
    },
    data () {
        return {

            invoiceTitle: "",
            invoiceTitleType: 1,        //1-个人，2-企业
            nsrsbh: "", 
            address: "",
            yhmz: "",
            yhzh: "",
            email: "",

            message: "",
            showMesage: false,

            showLoading: false,

            token: "",
            orderInfo: {},
        }
    },
    computed: {
        canSubmit() {
            return this.invoiceTitle && this.email && (this.invoiceTitleType !=2 || this.invoiceTitleType==2 && this.nsrsbh);
        }
    },
    mounted(){
        let {token} = this.$route.query;
        this.token = token;
        
        this.getOrderInfo();
    },
    destroyed(){

    },
    methods: {
        openMesage(msg){
            this.message = msg;
            this.showMesage = true;
        },
        onCloseMessage(){
            this.showMesage = false;
        },
        checkInput(){
            if(!/^.*@.*$/.test(this.email)){
                this.openMesage("请输入正确的邮箱");
                return false;
            }
            return true;
        },
        getOrderInfo(){
            this.showLoading = true;
            API["getOrderInfo"]({
                token: this.token
            }).then(res => {
                //res = testOrderInfo;

                // 已开过票
                if(res && res.code == 300888){
                    this.$router.push({name: "success"});
                    return;
                }
                if(!res || res.code != 100200){
                    res && this.openMesage(res.message);
                    return;
                }
                
                this.orderInfo = res && res.data || {};
            }).catch(err => {
                this.openMesage(err);
            }).then(()=>{
                this.showLoading = false;
            });
        },
        submit(){
            if(!this.canSubmit || !this.checkInput()){
                return;
            }
            let {invoiceTitle, invoiceTitleType, nsrsbh, address, yhzh, yhmz, email} = this;
            let orderId = this.orderInfo.orderId;

            this.showLoading = true;
            API["createInvoide"]({
                orderIds: orderId,
                invoiceTitleType: invoiceTitleType - 1,
                invoiceTitle,
                nsrsbh,
                address,
                bankName: yhmz,
                yhzh,
                email,
            }).then(res => {
                if(!res || res.code != 100200){
                    res && this.openMesage(res.message);
                    return;
                }
                
                this.$router.push({name: "success"});
            }).catch(err => {
                this.openMesage(err);
            }).then(()=>{
                this.showLoading = false;
            });
        },
        chooseInvoiceTitleType(n){
            this.invoiceTitleType = n;
        }
    }
}
</script>

<style lang="scss" scoped>
.createInvoice{
    background: #F5F4F4;
    min-height: 100vh;
    padding: 0 0 20px 0;
    position: relative;
    box-sizing: border-box;
    .head {
        height: 50px;
        line-height: 50px;
        background: #292B34;
        color: #fff;
        text-align: center;
        z-index: 1000;
        font-size: 18px;
    }
    .ul {
        padding: 0 20px;
        background: #fff;
        margin-top: 10px;
        font-size: 16px;
        &.nomargin{
            margin-top: 0;
        }
        .li {
            border-bottom: 1px solid #ddd;
            height: 50px;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            &:last-of-type {
                border-bottom: 0;
            }
            &.vt {
                flex-direction: column;
                align-items: stretch;
                height: auto;
                padding: 15px 0;
                .vl {
                    line-height: 20px;
                    display: flex;
                    &:not(:first-of-type){
                        margin-top: 5px;
                    }
                }
            }
            .lb {
                width: 120px;
                flex-shrink: 0;
                color: #333333;
            }
            .val {
                flex-grow: 1;
                font-size: 14px;
                text-align: right;
                color: #000;
                &.price {
                    color: #E6A940;
                }
            }
            .input {
                border: 0;
                outline: 0;
                width: 100%;
                display: block;
                height: 30px;
                flex-grow: 1;
                font-size: 14px;
                text-align: right;
                color: #000;
                &::placeholder{
                    color: #ccc;
                    text-align: right;
                }
                &::input-placeholder{
                    color: #ccc;
                    text-align: right;
                }
                &::-webkit-input-placeholder{
                    color: #ccc;
                    text-align: right;
                }
                &::-moz-placeholder{
                    color: #ccc;
                    text-align: right;
                }
                &::-ms-input-placeholder{
                    color: #ccc;
                    text-align: right;
                }
                &:focus{
                    text-align: left;
                }
                &:focus::input-placeholder{
                    color: transparent;
                }
                &:focus::placeholder{
                    color: transparent;
                }
                &:focus::-webkit-input-placeholder{
                    color: transparent;
                }
                &:focus::-moz-placeholder{
                    color: transparent;
                }
                &:focus::-ms-input-placeholder{
                    color: transparent;
                }
            }
            .ttlx {
                flex-grow: 1;
                text-align: right;
                .tti {
                    display: inline-block;
                    border: 1px solid #afa4a4;
                    padding: 2px 15px;
                    border-radius: 4px;
                    margin-left: 5px;
                    color: #000;
                    &.checked {
                        background: #afa4a4;
                        color: #fff;
                    }
                }
            }
        }
    }
    .tips {
        font-size: 12px;
        padding: 0 20px;
        margin: 20px 0;
        line-height: 17px;
        color: #666666;
    }
    .submitBtn {
        height: 44px;
        line-height: 44px;
        background: #E6A940;
        color: #fff;
        text-align: center;
        margin: 20px 20px 40px 20px;
        border-radius: 4px;
        &.gray {
            background: #ccc;
        }
    }
}
</style>



