<template>
    <div class="pdfView">
        <div class="head"> 查看电子发票</div>
        <!-- <pdf :src="pdfProxyPath"></pdf> -->
        <div class="pdfEmpty">暂不支持显示, 请下载后打开</div>
        <a :href="pdfPath" class="downloadBtn" download="invoice">下载</a>
        <!---->
        <message :msg="message" :show="showMesage" @close="onCloseMessage"></message>
        <loading v-if="showLoading"></loading>
    </div>
</template>

<script>
import API from "@/api";
import pdf from 'vue-pdf'
import loading from "@/commonComponents/loading.vue";
import message from "@/commonComponents/message.vue";

export default {
    components: {
        loading,
        message,
        pdf,
    },
    data () {
        return {
            token: "",
            pdfPath: "",        //下载地址
            pdfProxyPath: "",   //预览地址

            message: "",
            showMesage: false,

            showLoading: false,
        }
    },
    mounted(){
        let {token} = this.$route.query;
        this.token = token;
        
        this.getInvoice();
    },
    destroyed(){

    },
    methods: {
        getInvoice(){
            this.showLoading = true;
            API["getInvoice"]({
                token: this.token
            }).then(res => {
                if(!res || res.code != 100200){
                    res && this.openMesage(res.message);
                    return;
                }
                let { invoiceUrl, proxyUrl } = res.data || {};
                this.pdfPath = invoiceUrl;
                this.pdfProxyPath = proxyUrl;
            }).catch(err => {
                this.openMesage(err);
            }).then(()=>{
                this.showLoading = false;
            });
        },
        openMesage(msg){
            this.message = msg;
            this.showMesage = true;
        },
        onCloseMessage(){
            this.showMesage = false;
        },
    } 
}
</script>

<style lang="scss" scoped>
.pdfView {
    background: #F5F4F4;
    min-height: 100vh;
    padding: 0 0 20px 0;
    position: relative;
    box-sizing: border-box;
    .head {
        height: 50px;
        line-height: 50px;
        background: #292B34;
        color: #fff;
        text-align: center;
        font-size: 18px;
    }
    .pdfContainer {
        margin-top: 20px;
    }
    .pdfEmpty {
        margin-top: 20px;
        font-size: 14px;
        padding: 20px;
        text-align: center;
        color: #aaa;
    }
    .downloadBtn {
        text-decoration: none;
        display: block;
        height: 44px;
        line-height: 44px;
        background: #E6A940;
        color: #fff;
        text-align: center;
        margin: 20px;
        border-radius: 4px;
    }
}
</style>



