import Vue from 'vue';
import Router from 'vue-router';
import createInvoice from '@/components/createInvoice';
import success from '@/components/success';
import pdfView from '@/components/pdfView';

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: '/edispatch/invoice',
    // base: '/car-dispatch/invoice',
    routes: [
        {
            path: '/view/pdfview',
            name: 'pdfView',
            component: pdfView
        },
        {
            path: '/view/success',
            name: 'success',
            component: success
        },
        {
            path: '/view/create/',
            name: 'createInvoice',
            component: createInvoice
        },
        {
            path: '/',
            name: 'createInvoice',
            component: createInvoice
        },
    ]
})



