import request from '@/utils/request.js';

export default {
    // 批量开发票
    'createInvoide': (params = {}) => {
        return request({
            method: 'POST',
            url: "/create/batch",
            params: params
        });
    },
    //获取订单信息
    'getOrderInfo': (params = {}) => {
        return request({
            method: 'GET',
            url: "/order/byToken",
            params: params
        });
    },
    //获取发票地址
    'getInvoice': (params = {}) => {
        return request({
            method: 'GET',
            url: "/url/byToken",
            params: params
        });
    },
    //
};



