<template>
    <div class="loadingMask" @touchmove.prevent @mousewheel.prevent>
        <div class="loading">
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            
        }
    }
}
</script>

<style lang="scss" scoped>
.loadingMask{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.6);
    z-index: 1000;
    .loading {
        position: absolute;
        width: 50px;
        height: 50px;
        left: 50%;
        top: 50%;
        margin-left: -25px;
        margin-top: -25px;
        .dot {
            width: 4px;
            height: 4px;
            border-radius: 2px;
            background: #68b2ce;
            position: absolute;
            animation: demo3 linear 0.8s infinite;
        
            &:nth-child(1){
                left: 24px;
                top: 2px;
                animation-delay:0s;
            }
            &:nth-child(2){
                left: 40px;
                top: 8px;
                animation-delay:0.1s;
            }
            &:nth-child(3){
                left: 47px;
                top: 24px;
                animation-delay:0.1s;
            }
            &:nth-child(4){
                left: 40px;
                top: 40px;
                animation-delay:0.2s;
            }
            &:nth-child(5){
                left: 24px;
                top: 47px;
                animation-delay:0.4s;
            }
            &:nth-child(6){
                left: 8px;
                top: 40px;
                animation-delay:0.5s;
            }
            &:nth-child(7){
                left: 2px;
                top: 24px;
                animation-delay:0.6s;
            }
            &:nth-child(8){
                left: 8px;
                top: 8px;
                animation-delay:0.7s;
            }
        }
    }
}
@keyframes demo3 
{
    0%,40%,100% {transform: scale(1);}
    20% {transform: scale(3);}
}
</style>



