<template>
    <div class="message" :class="{show: show}">
        <div class="errInfo">{{msg}}</div>
    </div>
</template>

<script>
export default {
    props: ["msg", "show"],
    data () {
        return {
            closeTimer: null,
        }
    },
    watch: {
        show(){
            if(this.show){
                this.autoClose();
            }
        }
    },
    methods: {
        autoClose(){
            clearTimeout(this.closeTimer);
            this.closeTimer = setTimeout(()=>{
                this.$emit("close");
            }, 2000);
        }
    }
}
</script>

<style lang="scss" scoped>
.message{
    position: fixed;
    top: -44px;
    left: 0;
    right: 0;
    height: 44px;
    line-height: 44px;
    background: rgba(0,0,0,0.7);
    color: #fff;
    font-size: 14px;
    padding: 0 16px;
    z-index: 999;
    transition: top 0.2s;
    &.show {
        top: 50px;
    }
    .errInfo {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
</style>



